import { ACTION_TYPES } from "../constants/prd_const";

const INITIAL_STATE = {
    products: []
};

export const product_reducer = (state = INITIAL_STATE, { type, payload }) => {
    switch (type) {
        case ACTION_TYPES.SET_PRD:
            return {...state, products: payload }

        case ACTION_TYPES.SELECTED_PRODUCT:
            return {...state, ...payload }
        default:
            return state
    }
}