import React, { useEffect }  from 'react'
import { useDispatch, useSelector } from 'react-redux';
import axios from 'axios'
import {Link} from "react-router-dom";
import { set_prd } from './redux/actions/prdaction';
  

function Product() {

    const dispatch = useDispatch()

  const products = useSelector(state => state.product_reducer.products)

  const fetch_prd = async() => {
     axios.get('https://fakestoreapi.com/products')
          .then(res =>(
            dispatch(set_prd(res.data),
            console.log(res.data)
          )))
          .catch(err => {
            console.log(err);
    })
  }

  useEffect(() => {
    fetch_prd()
  }, [dispatch])
  


    return (
        <div className="container">
      <h2>Sunapana's Shopping Store</h2>
      <div className="store">
      {
        products.map((prd) => (
          <div className="card" key={prd.id}>
            <div className="img">
              <img src={prd.image} height="250px" alt={prd.title}/>
            </div>
            <div className="info">
              <div className="title">
                <p>{prd.title}</p>
              </div>
              <div className="price">
                <p>${prd.price}</p>
              </div>
            </div>
            <div className="btn">
              <Link to={`/product/${prd.id}`}>View</Link>
            </div>
          </div>
        ))
      }
      </div>
    </div>
    )
}

export default Product
