import { ACTION_TYPES } from "../constants/prd_const"

export const set_prd = (prd) => {
    return {
        type: ACTION_TYPES.SET_PRD,
        payload: prd
    }
}


export const selected_prd = (prd) => {
    return {
        type: ACTION_TYPES.SELECTED_PRODUCT,
        payload: prd
    }
}